.App {
    text-align: center;
}

.App-header {
    background-color: #7C9473;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #000000;
}

.App-avatar {
    margin-top: 5vh;
    width: 200px;
    height: 200px;
    box-sizing: border-box;
    overflow: hidden;
    border: 2px solid #000000;
    border-radius: 100%;
}

.App-footer {
    background-color: #494949;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
